a {
  display: flex;
  align-items: center;
  justify-content: center;
  .arrow-icon {
    position: relative;
    width: 1em;
  }

  .arrow-hover {
    border: 0.15625rem solid #53bfd2;
    border-radius: 50%;
    position: relative;
    display: block;
    width: 2.5rem;
    height: 2.5rem;
    margin: 0.5em 2em;

    cursor: pointer;

    svg {
      position: absolute;
      top: 50%;
      left: -1.1rem;
      width: 2.2rem;
      -webkit-transform: translateY(-50%);
      transform: translateY(-50%);
      transition: left ease 0.15s;
      fill: #32323c;
      cursor: pointer;
    }
  }

  &:hover {
    .arrow-icon {
      transition: 0.2s ease-in-out;
      left: 0rem;
    }
  }
}
